/* Categories.css */
.container {
  padding: 20px;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.col-md-6 {
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn-category {
  display: block;
  padding: 15px 30px;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s, transform 0.3s;
  text-decoration: none;
}

.btn-category:hover,
.btn-category:focus {
  background-color: #0056b3;
  transform: translateY(-2px);
}

.btn-category:active {
  background-color: #003d7a;
  transform: translateY(0);
}
