.user-menu {
  background-color: #f8f9fa; /* Light background color */
  border-radius: 0.5rem; /* Rounded corners */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Subtle shadow */
  border: 2px solid #dee2e6;
  display: flex; /* Light border for depth */
}

.user-menu-item {
  display: block;
  padding: 10px 15px;
  text-decoration: none;
  color: #343a40; /* Dark text color */
  font-size: 1rem;
  border-bottom: 1px solid #e9ecef; /* Divider between items */
}

.user-menu-item:hover {
  background-color: #e9ecef; /* Hover background color */
}

.user-menu-item.active {
  background-color: #007bff; /* Active background color */
  color: white; /* Text color for active item */
  font-weight: bold; /* Bold text for active item */
  border: none; /* Remove bottom border for active item */
}

.user-menu-item:last-child {
  border-bottom: none; /* Remove bottom border for last item */
}
