.product-img {
  width: 50px;
  height: 50px;
  object-fit: cover;
  border-radius: 5px;
}

.order-card {
  background-color: #f9f9f9;
  padding: 15px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.order-summary {
  margin-top: 10px;
  padding: 10px;
  background-color: #f1f1f1;
  border-radius: 5px;
}

.order-summary p {
  margin: 0;
  font-size: 0.9rem;
}
