/* Cart.css */
.cart-container {
  padding: 20px;
  background-color: #10100b48;
  display: flex;
  flex-direction: column;
}

.cart-header {
  text-align: center;
  margin-bottom: 30px;
  color: #110e0e;
  background-color: rgba(128, 128, 128, 0.559);
  padding: 20px;
  border-radius: 5px;
}

.cart-header h1 {
  font-size: 2rem;
  margin-bottom: 10px;
}

.cart-header h4 {
  font-size: 1.2rem;
  color: #555;
}

.cart-content {
  display: flex;
  justify-content: space-between;
}

.cart-items {
  flex: 0.7;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.cart-item {
  display: flex;
  border: 1px solid #110a0a53;
  padding: 15px 5px;
  margin-bottom: 10px;
  border-radius: 5px;
}

.cart-item-image img {
  max-width: 100px;
  border-radius: 8px;
}

.cart-item-details {
  margin-left: 20px;
}

.cart-item-details h5 {
  font-size: 1.2rem;
  color: #333;
}

.cart-item-details p {
  margin: 5px 0;
  color: #777;
}

.cart-item-price {
  font-weight: bold;
  color: #000;
}

.cart-summary {
  flex: 0.3;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.cart-summary h3 {
  margin-bottom: 20px;
  font-size: 1.5rem;
  color: #333;
}

.cart-summary p {
  margin: 10px 0;
  font-size: 1rem;
  color: #555;
}

.checkout-btn {
  padding: 10px 20px;
  background-color: #000306;
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 20px;
  transition: background-color 0.3s ease;
}

.checkout-btn:hover {
  background-color: #185be2;
}
@media (max-width: 800px) {
  .cart-content {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
  }
  .cart-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .cart-summary {
    margin-top: 10px;
  }
}
