/* Policy.css */
.policy {
  padding: 60px 20px;
  background: linear-gradient(135deg, #c8e1f9, #e0e6ed);
  color: #333;
}

.policy-title {
  font-size: 2.8rem;
  color: #212529; /* Darker shade for better contrast */
  margin-bottom: 40px;
  font-weight: 700;
  text-align: center;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.15); /* Subtle shadow for depth */
}

.policy-content {
  max-width: 1200px; /* Increased max-width for more spacious content */
  margin: 0 auto;
  background: #ffffff;
  border-radius: 12px; /* Slightly rounded corners */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15); /* Slightly deeper shadow for more emphasis */
  padding: 40px; /* Increased padding for a more spacious feel */
}

.policy-content h2 {
  font-size: 1.9rem; /* Slightly larger heading size */
  color: #0056b3; /* A deeper blue for headings */
  margin-bottom: 25px;
  font-weight: 700;
}

.policy-content p {
  font-size: 1.2rem; /* Slightly larger font size for better readability */
  margin-bottom: 25px;
  line-height: 1.8; /* Increased line-height for improved readability */
  color: #495057; /* Softer dark gray for text */
}

/* Links in the content */
.policy-content a {
  color: #007bff;
  text-decoration: none;
  font-weight: 500;
}

.policy-content a:hover {
  text-decoration: underline;
}

/* Responsive Design */
@media (max-width: 768px) {
  .policy-content {
    padding: 20px;
  }

  .policy-title {
    font-size: 2.2rem;
  }

  .policy-content h2 {
    font-size: 1.6rem;
  }

  .policy-content p {
    font-size: 1rem;
  }
}
