/* Card styling */
.dashboard-content .card {
  background: #ffffff; /* Clean white background */
  border-radius: 1rem; /* Rounded corners */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1); /* More pronounced shadow for depth */
  border: none; /* Remove default border */
  padding: 2rem; /* Increased padding inside the card */
  transition: transform 0.3s ease, box-shadow 0.3s ease; /* Smooth transitions */
}

.dashboard-content .card:hover {
  transform: translateY(-5px); /* Lift the card on hover */
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15); /* Deeper shadow on hover */
}

/* Typography for card title */
.card h3 {
  color: #343a40; /* Dark color for the title */
  font-size: 2rem; /* Larger font size for emphasis */
  font-weight: 700; /* Bold text for the title */
  margin-bottom: 1rem; /* Space below the title */
}

/* User info section */
.user-info {
  margin-top: 1.5rem; /* Space above user info */
}

.user-info p {
  font-size: 1.125rem; /* Slightly larger font size for better readability */
  color: #495057; /* Medium gray color for text */
  margin-bottom: 0.75rem; /* Space between paragraphs */
  line-height: 1.5; /* Improved line height for readability */
}

/* Add subtle separators between user info items */
.user-info p::before {
  content: "â€¢";
  margin-right: 0.5rem;
  color: #007bff; /* Accent color for the separator */
  font-size: 1.5rem; /* Slightly larger separator */
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .col-md-3,
  .col-md-9 {
    padding: 0.5rem; /* Reduced padding on smaller screens */
  }
}
