/* HomePage.css */

.category-title {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #003366;
}

.results-count {
  margin-bottom: 2rem;
  font-size: 1.2rem;
  color: #666666;
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 20px;
  padding: 20px;
}

.product-card {
  border-radius: 10px;
  width: 230px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease-in-out;
  background-color: #ffffff;
}

.product-card:hover {
  transform: scale(1.05);
}

.product-image {
  height: 200px;
  object-fit: cover;
  width: 100%;
}

.product-name {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  color: #003366;
}

.product-description {
  font-size: 1rem;
  margin-bottom: 0.5rem;
  color: #666666;
}

.product-price {
  font-size: 1.2rem;
  margin-bottom: 1rem;
  color: #000000;
}

.add-to-cart-btn {
  width: 100%;
  background-color: #003366;
  border: none;
  color: #ffffff;
  padding: 10px;
  font-size: 1rem;
}

.add-to-cart-btn:hover {
  background-color: #0059b3;
}

.sidebar-2 {
  background-color: rgb(209, 155, 155);
  margin-top: 0;
  padding: 20px;
  height: 100vh;
}
.bg-row {
  background-color: rgba(192, 163, 126, 0.297);
  color: rgb(7, 3, 3);
}
.banner {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  background: linear-gradient(135deg, #ff7e5f, #feb47b);
}
.banner-image {
  width: 500px;
  margin-right: 30px;
  margin: 20px;
  border-radius: 50px;
}
@media (max-width: 800px) {
  .product-card {
    width: 250px;
    margin: 0 auto;
  }
  .product-image {
    width: 100%;
  }
  .banner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
  }
  .banner-image {
    width: 300px;
  }
  .sidebar-2 {
    height: auto;
    background-color: #efb4b4;
  }
}
