.create-category-container {
  padding: 20px;
  background-color: #14020d70; /* Light background for better contrast */
}

.header {
  margin-bottom: 1rem;
}

.header h1 {
  font-size: 2rem;
  font-weight: bold;
  color: #333;
  text-align: center;
}

.form-container {
  margin-bottom: 2rem;
  padding: 1rem;
  background: #ffffff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.table-container {
  margin-top: 2rem;
}

.ant-table {
  border-radius: 8px;
  overflow: hidden;
  background: #ffffff;
  border: 1px solid black;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.ant-table-thead > tr > th {
  background-color: #636f2bf5;
  color: #000;
  font-weight: 700;
  font-size: 20px;
}

.ant-table-tbody > tr > td {
  color: #333;
  font-size: 16px;
  border: 1px solid black;
  background-color: #f1d9d9;
}

.actions .ant-btn {
  margin-right: 8px;
}

.edit-category-modal .ant-modal-header {
  background-color: #f0f2f5;
}

.edit-category-modal .ant-modal-footer {
  border-top: 1px solid #e8e8e8;
}

.ant-table-striped > tbody > tr:nth-child(odd) {
  background-color: #fafafa;
}
