/* About.css */
.about {
  padding: 60px 20px;
  background: linear-gradient(135deg, #c8e1f9, #e0e6ed);
  color: #333;
}

.about-text {
  margin-bottom: 40px;
}

.about-text h1 {
  font-size: 3rem;
  color: #212529;
  margin-bottom: 10px;
  font-weight: 700;
}

.about-text p {
  font-size: 1.2rem;
  color: #555;
  line-height: 1.6;
}

.about-content {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: 40px;
  justify-content: center;
  max-width: 1200px;
  margin: 0 auto;
}

.about-image img {
  width: 100%;
  max-width: 500px;
  height: 700px;
  border-radius: 10px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
}

.about-me-info {
  flex: 1;
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  padding: 30px;
}

.about-me-info h2 {
  font-size: 1.75rem;
  color: #0056b3;
  margin-bottom: 20px;
  font-weight: 700;
}

.about-me-info p {
  font-size: 1.1rem;
  margin-bottom: 20px;
  line-height: 1.6;
  color: #333;
}

/* Responsive Design */
@media (max-width: 768px) {
  .about-content {
    flex-direction: column;
    align-items: center;
  }

  .about-image {
    margin-bottom: 20px;
  }
}
