/* Footer.css */
.footer {
  background-color: #333;
  color: #f1f1f1;
  padding: 20px;
  text-align: center;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
}

.footer-text {
  margin-bottom: 20px;
  font-size: 1rem;
}

.footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  gap: 20px;
}

.footer-item {
  display: inline;
}

.footer-link {
  color: #61dafb; /* Light blue color for links */
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s;
}

.footer-link:hover {
  color: #21a0f6; /* Darker blue color on hover */
}

.footer-link.active {
  font-weight: bold;
}

.footer-social {
  margin-top: 20px;
}

.social-icon {
  color: #61dafb; /* Light blue color for icons */
  font-size: 1.5rem;
  margin: 0 10px;
  transition: color 0.3s;
}

.social-icon:hover {
  color: #21a0f6; /* Darker blue color on hover */
}

/* Responsive design for smaller screens */
@media (max-width: 768px) {
  .footer-links {
    flex-direction: column;
    gap: 10px;
  }
}
