/* Contact.css */
.contact {
  padding: 60px 20px;
  background: linear-gradient(135deg, #c8e1f9, #e0e6ed);
  color: #333;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact-title {
  font-size: 2.5rem;
  color: #212529;
  margin-bottom: 40px;
  font-weight: 700;
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
}

.contact-content {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: 40px;
  width: 100%;
  max-width: 1200px; /* Container width */
}

.contact-info,
.contact-form {
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1); /* Enhanced shadow for depth */
  padding: 40px;
  width: 100%;
  max-width: 600px; /* Max width for responsive design */
}

.contact-info h2,
.contact-form h2 {
  font-size: 1.75rem;
  color: #0056b3;
  margin-bottom: 20px;
  font-weight: 700; /* Bold text */
}

.contact-info p {
  font-size: 1.1rem;
  margin-bottom: 20px;
  line-height: 1.6; /* Improve readability */
  color: #555; /* Slightly lighter text color */
}

.contact-info ul {
  list-style: none;
  padding: 0;
}

.contact-info li {
  font-size: 1.1rem;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  color: #333; /* Match text color */
}

.contact-info i {
  font-size: 1.4rem;
  color: #0056b3; /* Accent color */
  margin-right: 12px;
}

.contact-info a {
  color: #0056b3;
  text-decoration: none;
}

.contact-info a:hover {
  text-decoration: underline;
}

.contact-form .form-group {
  margin-bottom: 24px;
}

.contact-form label {
  display: block;
  font-size: 1.1rem;
  margin-bottom: 6px;
  color: #333; /* Match text color */
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 14px;
  font-size: 1rem;
  border: 1px solid #ddd;
  border-radius: 6px;
  transition: border-color 0.3s, box-shadow 0.3s;
}

.contact-form input:focus,
.contact-form textarea:focus {
  border-color: #0056b3;
  outline: none;
  box-shadow: 0 0 8px rgba(0, 86, 179, 0.2); /* Highlight on focus */
}

.contact-form textarea {
  resize: vertical;
  min-height: 150px; /* Minimum height for better usability */
}

.btn-submit {
  display: inline-block;
  padding: 14px 28px;
  font-size: 1.2rem;
  color: #ffffff;
  background-color: #0056b3;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  transition: background-color 0.3s, box-shadow 0.3s;
}

.btn-submit:hover {
  background-color: #004494;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

/* Responsive Design */
@media (max-width: 768px) {
  .contact-content {
    flex-direction: column;
    align-items: center;
  }

  .contact-info,
  .contact-form {
    max-width: 100%; /* Full width on smaller screens */
  }
}
