/* NotFound.css */
.not-found {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(135deg, #f0f4f8, #e0e6ed);
  text-align: center;
  padding: 0 20px;
}

.not-found-content {
  max-width: 500px;
  padding: 30px;
  background: #ffffff;
  border-radius: 15px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
  border: 1px solid #e0e0e0;
}

.not-found-title {
  font-size: 8rem;
  color: #ff6f61;
  margin-bottom: 20px;
  font-weight: 700;
  text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.1);
}

.not-found-message {
  font-size: 1.2rem;
  color: #555;
  margin-bottom: 30px;
  line-height: 1.6;
}

.not-found-link {
  display: inline-block;
  padding: 12px 24px;
  font-size: 1.1rem;
  color: #ffffff;
  background-color: #007bff;
  border-radius: 25px;
  text-decoration: none;
  transition: background-color 0.3s, box-shadow 0.3s;
  font-weight: 500;
}

.not-found-link:hover {
  background-color: #0056b3;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

@media (max-width: 768px) {
  .not-found-title {
    font-size: 6rem;
  }

  .not-found-content {
    padding: 20px;
  }
}
