.sidebar {
  background-color: #343a40;
  color: #ffffff;
  padding: 1.5rem;

  height: 100vh;
}

.sidebar .list-group-item {
  background-color: #343a40;
  color: #ffffff;
  border: none;
  border-radius: 5px;
  margin-bottom: 0.5rem;
}

.sidebar .list-group-item.active {
  background-color: #007bff;
  color: #ffffff;
}

.card {
  background-color: #ffffff;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 2rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.card h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #333;
}

.card p {
  font-size: 1.1rem;
  color: #555;
  margin-bottom: 0.5rem;
}

.card strong {
  color: #333;
}

@media (max-width: 768px) {
  .sidebar {
    padding: 1rem;
    height: 30vh;
  }

  .card {
    padding: 1.5rem;
  }
}
