.admin-menu {
  background-color: #ffffff; /* Clean white background */
  border-radius: 0.75rem; /* Slightly more rounded corners */
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15); /* Soft shadow for depth */
  border: 1px solid #dee2e6; /* Light border for a cleaner look */
  padding: 0.5rem; /* Padding inside the container */
}

.admin-menu-item {
  display: block;
  padding: 12px 20px; /* Increased padding for better spacing */
  text-decoration: none;
  color: #495057; /* Slightly lighter dark text color */
  font-size: 1rem;
  border-radius: 0.5rem; /* Rounded corners for items */
  margin-bottom: 0.5rem; /* Space between items */
  transition: background-color 0.3s, color 0.3s; /* Smooth transition for hover effects */
}

.admin-menu-item:hover {
  background-color: #f1f3f5; /* Slightly darker hover background color */
  color: #212529; /* Darker text color on hover */
}

.admin-menu-item.active {
  background-color: #007bff; /* Blue background for active item */
  color: #ffffff; /* White text color for active item */
  font-weight: 600; /* Semi-bold text for active item */
  border: none; /* No border for active item */
}

.admin-menu-item:last-child {
  margin-bottom: 0; /* Remove bottom margin for last item */
}
