.create-product-container {
  padding: 20px;
  max-width: 900px;
  margin: auto;
  background-color: #333;
  padding: 10px;
  margin: 10px auto;
}

.create-product-form {
  background-color: #f9f9f9;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.create-product-form h1 {
  font-size: 24px;
  margin-bottom: 20px;
  color: #333;
}

.category-select {
  width: 100%;
  margin-bottom: 20px;
}

.photo-upload {
  margin-bottom: 20px;
}

.upload-label {
  display: inline-block;
  padding: 10px 20px;
  border: 2px solid #d9d9d9;
  border-radius: 4px;
  cursor: pointer;
  background-color: #ffffff;
  color: #333;
  font-size: 14px;
  font-weight: 700;
}

.upload-label:hover {
  border-color: #40a9ff;
  background-color: #14020d70;
}

.form-group {
  margin-bottom: 20px;
}

.form-input,
.form-textarea {
  width: 100%;
  border-radius: 4px;
  background-color: #c9e4fa;
}

.form-input {
  padding: 10px;
  border: 1px solid #d9d9d9;
  color: black;
  font-size: 16px;
  font-weight: 500;
  text-transform: capitalize;
}

.form-textarea {
  padding: 10px;
  border: 1px solid #d9d9d9;
  color: black;
  font-size: 16px;
  font-weight: 500;
  text-transform: capitalize;
}

.submit-button {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border-radius: 4px;
  background-color: green;
}
