.category-title {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #003366;
}

.results-count {
  margin-bottom: 2rem;
  font-size: 1.2rem;
  color: #666666;
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  padding: 20px;
}

.product-card {
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease-in-out;
}

.product-card:hover {
  transform: scale(1.05);
}

.product-image {
  height: 200px;
  object-fit: cover;
}

.product-name {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  color: #003366;
}

.product-description {
  font-size: 1rem;
  margin-bottom: 0.5rem;
  color: #666666;
}

.product-price {
  font-size: 1.2rem;
  margin-bottom: 1rem;
  color: #000000;
}

.add-to-cart-btn {
  width: 100%;
  background-color: #003366;
  border: none;
}

.add-to-cart-btn:hover {
  background-color: #0059b3;
}
