.orders-container {
  background-color: #f8f9fa;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.orders-container h1 {
  font-family: "Arial", sans-serif;
  color: #343a40;
  font-weight: bold;
}

.table {
  background-color: #ffffff;
  border-radius: 8px;
  overflow: hidden;
}

.table thead {
  background-color: #343a40;
  color: #ffffff;
}

.table-hover tbody tr:hover {
  background-color: #e9ecef;
}

.table th,
.table td {
  vertical-align: middle;
  text-align: center;
}

.table td {
  font-size: 0.9rem;
  color: #495057;
}

.table td:nth-child(4),
.table td:nth-child(5) {
  font-weight: bold;
}

.text-center p {
  font-size: 1.2rem;
  color: #6c757d;
}

.product-img {
  width: 50px;
  height: 50px;
  object-fit: cover;
  border-radius: 5px;
}

.table-hover tbody tr:hover {
  background-color: #f8f9fa;
  border-bottom: 1px solid rgb(0, 0, 0);
}

.order-summary {
  margin-top: 10px;
  padding: 10px;
  background-color: #f1f1f1;
  border-radius: 5px;
}

.order-summary p {
  margin: 0;
  font-size: 0.9rem;
}
